.dates {
  padding-top: 23px;

  @media (min-width: $tablet-width) {
    padding-top: 58px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 33px;
    width: 100%;
  }
}

.dates__month {
  margin-bottom: 22px;
  padding-top: 11px;
  padding-bottom: 11px;
  border-top: 1px solid $calendar-grey;
  border-bottom: 1px solid $calendar-grey;

  @media (min-width: $tablet-width) {
    margin-bottom: 39px;
    padding-top: 27px;
    padding-bottom: 23px;
  }
}
